@import "../../../styles/mixins.scss";

.group {
    flex: 0 0 50%;

    &:first-child {
        padding-right: 16px;

        @include breakpoint(m) {
            padding-right: 0;
        }
    }

    &:nth-child(2) {
        padding-left: 16px;

        @include breakpoint(m) {
            padding-left: 0;
        }
    }

    &:not(:last-child) {
        @include breakpoint(m) {
            border-bottom: 1px solid $neutrals6;

            @include dark {
                border-color: $neutrals3;
            }
        }
    }

    &.active {
        @include breakpoint(m) {
            .head {
                svg {
                    transform: translateY(-50%) rotate(180deg);
                }
            }

            .menu {
                display: flex;
            }
        }
    }


    .head {
        margin-bottom: 40px;
        @include body-bold-2;

        @include breakpoint(t) {
            margin-bottom: 32px;
        }

        @include breakpoint(m) {
            position: relative;
            margin-bottom: 0;
            padding: 32px 32px 32px 0;
            @include hairline-2;
        }

        svg {
            display: none;

            @include breakpoint(m) {
                display: block;
                position: absolute;
                top: 50%;
                right: 10px;
                transform: translateY(-50%);
                fill: $neutrals4;
                transition: transform .2s;
            }
        }
    }

    .menu {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        @include breakpoint(m) {
            display: none;
            padding-bottom: 32px;
        }

        .link {
            @include button-2;
            color: $neutrals4;
            transition: color .2s;

            &:hover {
                color: $blue;
            }

            &:not(:last-child) {
                margin-bottom: 24px;
            }
        }
    }
}
