@import "../../../../styles/mixins.scss";

.section {
    .container {
        .head {
            max-width: 600px;
            margin: 0 auto 0 auto;
            text-align: center;

            @include breakpoint(x) {
                margin-bottom: 112px;
            }

            @include breakpoint(m) {
                margin-bottom: 64px;
            }


            .stage {
                margin-bottom: 8px;
                @include hairline-2;
                color: $neutrals4;
            }


            .title {
                margin-bottom: 24px;

                @include breakpoint(m) {
                    font-size: 32px;
                }
            }

            .button {
            }
        }
    }
}
