@import "../../../../styles/mixins.scss";



.section {
    overflow: hidden;

    .container {

        .wrapper {
            position: relative;


            .title {
                margin-bottom: 64px;

                @include breakpoint(m) {
                    margin-bottom: 48px;
                }
            }

            .inner {
                margin: 0 -16px;

                @include breakpoint(m) {
                    margin: 0 -8px;
                }


                .card {
                    width: 100%;
                }
            }
        }
    }
}
