@import './variables';
@import './mixins';

.popup-overlay {
    background: rgba(0, 0, 0, 0.7);
}

[data-popup='tooltip'].popup-overlay {
    background: transparent;
}

.popup-content {
    margin: auto;
    background: $neutrals1;
    color: $neutrals8;
    width: 50%;
    padding: 5px;
    border-radius:5px;

    @include dark-common {
        background: $neutrals8;
        color: $neutrals1;
    }
}

.popup-arrow {
    color: $neutrals1;

    @include dark-common {
        color: $neutrals8;
    }
}

[role='tooltip'].popup-content {
    width: auto;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 3px;
}
