@import "../../../../styles/mixins.scss";

.section {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 800px;
    padding: 80px 0;
    border-top: 1px solid $neutrals6;

    @include breakpoint(d) {
        min-height: 576px;
    }

    @include breakpoint(m) {
        display: block;
        min-height: auto;
        padding: 64px 0;
        text-align: center;
    }

    @include dark {
        border-color: $neutrals3;
    }

    .container {

        .wrap {
            position: relative;
            z-index: 3;
            max-width: 480px;

            @include breakpoint(d) {
                max-width: 420px;
            }

            @include breakpoint(m) {
                max-width: 100%;
                margin-bottom: 40px;
            }

            .stage {
                margin-bottom: 12px;
                @include hairline-1;
                color: $neutrals4;
            }

            .title {
                margin-bottom: 20px;

                @include breakpoint(m) {
                    font-size: 48px;
                }
            }

            .text {
                margin-bottom: 40px;
                @include body-2;
                color: $neutrals4;
            }

            .btns {
                display: flex;

                @include breakpoint(m) {
                    display: block;
                }

                .button {
                    @include breakpoint(m) {
                        width: 100%;
                    }

                    &:not(:last-child) {
                        margin-right: 16px;

                        @include breakpoint(m) {
                            margin: 0 0 16px;
                        }
                    }
                }
            }
        }

        .gallery {
            position: absolute;
            top: 50%;
            right: calc(50% - 560px);
            width: 684px;
            transform: translateY(-50%);
            pointer-events: none;

            @include breakpoint(x) {
                right: calc(50% - 520px);
                width: 550px;
            }

            @include breakpoint(d) {
                right: calc(50% - 440px);
                width: 470px;
            }

            @include breakpoint(m) {
                position: static;
                transform: translateY(0);
            }

            @include breakpoint(s) {
                width: auto;
                margin: 0 0 0 -22px;
            }

            .preview {
                &:first-child {
                    position: relative;
                    z-index: 2;
                }

                &:nth-child(2) {
                    right: -11%;
                    bottom: -3.5%;
                    z-index: 3;
                    width: 47%;
                }

                &:not(:first-child) {
                    position: absolute;
                }

                img {
                    width: 100%;
                }
            }
        }
    }
}
