    .slick-list {
        overflow: visible;

        @include breakpoint(s) {
            margin: 0 -215px 0 0;
        }
    }

    .slick-track {
        display: flex;
    }

    .slick-slide {
        height: auto;
        margin: 0 16px;
        opacity: 0;
        transition: opacity .4s;

        @include breakpoint(m) {
            margin: 0 8px;
        }

        &.slick-active {
            opacity: 1;
        }

        & > div {
            display: flex;
            height: 100%;
            flex-grow: 1;
        }
    }

    .slick-arrow {
        top: 4px;
    }

    .slick-prev {
        right: 48px;
    }

    .slick-next {
        right: 0;
