@import "../../styles/mixins.scss";

.header {
    position: relative;
    padding: 20px 0;
    z-index: 20;
    border-bottom: 1px solid $neutrals6;

    @include breakpoint(m) {
        padding: 48px 0 24px;
        border: none;
    }

    @include dark {
        border-color: $neutrals3;
    }

    .container {
        display: flex;
        align-items: center;

        .button {
            margin-right: 12px;

            @include breakpoint(m) {
                display: none;
            }
        }


        .logo {
            @include background-image('logo_01', 'png', cover, initial, false);
            position: relative;
            z-index: 12;
            display: inline-block;
            flex-shrink: 0;
            width: 260px;
            height: 43px;
            margin-right: 32px;


            @include dark {
                @include background-image('logo_dark_01', 'png', cover, initial, false);
            }
        }

        .wrapper {
            display: flex;
            align-items: center;
            flex-grow: 1;

            @include breakpoint(d) {
                flex-grow: 0;
            }

            @include breakpoint(m) {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                flex-direction: column;
                align-items: stretch;
                height: 100vh;
                margin: 0;
                padding: 140px 32px 40px;
                box-shadow: 0px 64px 64px rgba(31, 47, 70, 0.2);
                background: $neutrals8;
                visibility: hidden;
                opacity: 0;
                transition: all .2s;

                @include dark {
                    background: $neutrals1;
                }

                &:active {
                    visibility: visible;
                    opacity: 1;
                }
            }
        }

        .nav {
            display: flex;
            margin-right: auto;
            border-left: 2px solid $neutrals6;

            @include breakpoint(d) {
                display: none;
            }

            @include breakpoint(m) {
                display: flex;
                flex-direction: column;
                margin: 0 0 40px;
                border: none;
            }

            @include dark {
                border-color: $neutrals3;
            }
        }

        .search {
            width: 256px;
            flex-shrink: 0;
            margin-right: 24px;
            position: relative;

            @include breakpoint(t) {
                display: none;
            }

            @include breakpoint(m) {
                display: block;
                width: 100%;
                margin: auto 0 0;
            }

            .input {
                @include poppins;
                @include caption-2;
                transition: border-color .2s;
                width: 100%;
                height: 40px;
                padding: 0 42px 0 16px;
                background: none;
                border: 2px solid $neutrals6;
                border-radius: 8px;

                &::placeholder {
                    color: $neutrals4;
                }

                @include dark {
                    border-color: $neutrals3;
                    color: $neutrals8;
                }

                &:focus {
                    border-color: $neutrals4;
                }
            }

            .result {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                width: 42px;

                svg {
                    fill: $neutrals4;
                    transition: fill .2s;
                }

                &:hover {
                    svg {
                        fill: $blue;
                    }
                }
            }
        }


        .notification {
            margin-right: 24px;

            @include breakpoint(m) {
                margin-right: 20px;
            }
        }

        .user {
        }
    }
}
